import * as Actions from './action';

const initState = {
    darkmode: localStorage.getItem('darkmode') === 'true' ? true : false,
}


export function DarkmodeReducer(state = initState, action) {
    switch (action.type) {
        case Actions.DISABLE_DARKMODE:
            return {
                ...state,
                darkmode:false
            }
        case Actions.ENABLE_DARKMODE:
            return {
                ...state,
                darkmode:true
            }
        case Actions.INVERT_DARKMODE:
            localStorage.setItem('darkmode', !state.darkmode);
            return {
                ...state,
                darkmode : !state.darkmode
            }
        default:
            return state;
    }
}