export const ENABLE_DARKMODE = 'ui/ENABLE_DARKMODE';
export const DISABLE_DARKMODE = 'ui/DISABLE_DARKMODE';
export const INVERT_DARKMODE = 'ui/INVERT_DARKMODE';



export const EnableDarkmode = () => {
    return {
        type: ENABLE_DARKMODE
    }
}

export const DisableDarkmode = () => {
    return {
        type: DISABLE_DARKMODE
    }
}

export const InvertDarkmode = () => {
    return {
        type: INVERT_DARKMODE
    }
}