import React from 'react';
import 'styles/pages/AboutPage.scss';
import DSHS from 'assets/imgs/dshs.svg';
import SCHOOL from 'assets/imgs/school.svg';
import FINDER from 'assets/imgs/finder.svg';

import Skill1 from 'assets/imgs/skill1.svg';
import Skill2 from 'assets/imgs/skill2.svg';

function AboutPage() {
    return (
        <div className='AboutPage'>
            <section className='About1'>
                <div className="Stack">
                    <img className='SchoolImg' src={DSHS} alt="" style={{ height: '100%' }} />
                    <div className="Block">
                        <h4 className='w7'></h4>
                        <p className='w4'>              .        ,            .</p>
                    </div>


                    <div className="Block Web popEffect" style={{ backgroundColor: 'rgba(41, 232, 155, 0.2)' }}>
                        <img src={SCHOOL} alt="" />
                        <h5 className='w7' onClick={() => { window.open('https://dshs.kr', '__blank') }}> <br /></h5>
                    </div>
                </div>
                <h1 className='w6'> <br /><span className='green w7'></span><br />.</h1>
            </section>


            <section className='About2'>
                <div className="Stack">
                    <div className="Block Finder">
                        <img src={FINDER} alt="FINDER" className='FinderLogo' />
                    </div>

                    <div className="Block">
                        <h4 className='w7'>FINDER</h4>
                        <p className='w4'>   STEAM  , MAKER     .  4        .</p>
                    </div>
                </div>
                <h1 className='w6'>  <br /><span className="blue w7">FINDER</span><br />,</h1>

            </section>


            <section className='About3'>
                <div className="Stack">
                    <div className="Skill">
                        <img src={Skill1} alt="" />
                        <span>
                            <h2></h2>
                            <h6 className='w4'>Artificail Intelligence</h6>
                        </span>
                    </div>

                    <div className="Skill">
                        <img src={Skill2} alt="" />
                        <span>
                            <h2>/ </h2>
                            <h6 className='w4'>Web/App Development</h6>
                        </span>
                    </div>
                </div>

                <h1 className='w6'><span className="green w7">SW </span><br /> .</h1>
            </section>
        </div>
    );
}

export default AboutPage;