import React from 'react';
import 'styles/pages/SkillPage.scss';
import PythonLogo from 'assets/lang/python.svg';
import JSLogo from 'assets/lang/js.svg';
import Typewriter from "typewriter-effect";



function SkillBlock({ KorName, EngName, Library, Img }) {
    return (
        <div className="SkillGrid">
            <div className="ContentFrame">
                <h4 className='w5'>    </h4>
                <h2>{Library}</h2>
            </div>

            <div className="ImgFrame">
                <img src={Img} alt="" />
            </div>
            <div className="TextFrame">
                <h2>{KorName}</h2>
                <p className='w5'>{EngName}</p>
            </div>
        </div>

    )
}

function SkillPage() {

    return (
        <div className='Skillpage'>
            <section className='SkillSection'>
                <h1>   <br /><span className="green w7"> </span><br />   !</h1>
                <Typewriter
                    options={{ autoStart: true }}
                    onInit={(typewriter) => {
                        typewriter
                            .typeString('#include < iostream >')
                            .pauseFor(1000)
                            .typeString('<br/>import tensorflow as tf')
                            .pauseFor(1000)
                            .typeString('<br/>import React from "react";')
                            .pauseFor(1000)
                            .typeString('<br/>...')
                            .pauseFor(1000)
                            .start();
                    }}
                />
            </section>
            <div className="Block">
                <SkillBlock KorName='' EngName='Python' Img={PythonLogo} Library='Tensorflow(Keras), Flask, OpenCV, Pandas, Numpy...'></SkillBlock>
                <SkillBlock KorName='' EngName='Javascript' Img={JSLogo} Library='React, React Native, Express...'></SkillBlock>
            </div>
        </div>
    );
}

export default SkillPage;