import React, { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { CloseModal } from 'store/modal/action';
import 'styles/components/Modal.scss';

function Modal() {

    const ModalRef = useRef();

    useEffect(() => {
        document.body.classList.add('ModalEnable');
        ModalRef.current.classList.remove('ModalLoad');
        return () => {
            document.body.classList.remove('ModalEnable');
        }
    }, []);

    const state = useSelector((state) => state.Modal);
    const dispatch = useDispatch();

    function Close() {
        dispatch(CloseModal());
    }

    return (
        <div className="ModalFrame ModalLoad" ref={ModalRef}>
            <button className='w7' onClick={Close}></button>
            <div className="Modal">
                {state.component}
            </div>
        </div>
    );
}

export default Modal;