import React from 'react';
import GithubLogo from 'assets/imgs/github.svg';
import Night from 'assets/imgs/night.svg';
import 'styles/components/Shortcut.scss';
import { useDispatch } from 'react-redux';
import { InvertDarkmode } from 'store/darkmode/action';

function Shortcut() {
    const dispatch = useDispatch();

    function OpenPopup(url) {
        window.open(url, '__blank');
    }
    return (
        <div className="Shortcut">
            <img src={GithubLogo} className='popEffect' onClick={() => {OpenPopup('https://github.com/rzbsys')}} alt="" />
            <img src={Night} alt="" onClick={() => {dispatch(InvertDarkmode())}} className='popEffect' />
        </div>
    );
}

export default Shortcut;