import React, {memo} from 'react';
import 'styles/components/Gallery.scss';

import { Portfolio } from 'assets/portfolio';

function ExtractLogo(array) {
    var Data = []
    for (var i = 0; i < array.length; i++) {
        Data.push(array[i].Logo);
    }
    Data.sort(() => Math.random() - 0.5);
    return Data;
}



function Line({ direction = 'right', array, className = '' }) {

    function FirstLoadingEffect(e) {
        var RandomSec = Math.random() * 800;
        setTimeout(() => {
            e.target.classList.remove('FirstLoading');
        }, RandomSec);
    }

    return (
        <div className={"Line First " + className}>
            {
                array.map((value, index) => {
                    return (
                        <img className='FirstLoading' key={index} src={value} alt="" onLoad={(e) => { FirstLoadingEffect(e) }} />
                    )
                })
            }
        </div>

    )
}

function Gallery() {

    return (
        <div className="Gallery">
            <Line className='First' array={ExtractLogo(Portfolio)}></Line>
            <Line className='Second' array={ExtractLogo(Portfolio)}></Line>
            <Line className='Third' array={ExtractLogo(Portfolio)}></Line>
        </div>
    );
}

export default memo(Gallery);