import React from 'react';
import { Route, Routes, useLocation } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import { useSelector } from 'react-redux';

import Navbar from 'components/Navbar';
import MainPage from './MainPage';
import AboutPage from './AboutPage';
import SkillPage from './SkillPage';
import PortfolioPage from './PortfolioPage';
import ContactPage from './ContactPage';

import 'styles/anime.scss';



function InnerRoute() {
    return (
        <Routes>
            <Route path='about' element={<AboutPage></AboutPage>}></Route>
            <Route path='skill' element={<SkillPage></SkillPage>}></Route>
            <Route path='portfolio' element={<PortfolioPage></PortfolioPage>}></Route>
            <Route path='contact' element={<ContactPage></ContactPage>}></Route>
        </Routes>
    )
}

function RouterApp() {
    const state = useSelector((state) => state.Darkmode);
    if (state.darkmode === true) {
        document.body.classList.add('animationAll');
        document.body.classList.add('darkmode');
        setTimeout(() => {
            document.body.classList.remove('animationAll');
        }, 500);
    } else {
        document.body.classList.remove('darkmode');
    }
    const location = useLocation();
    return (
        <>
            <TransitionGroup component={null}>
                <CSSTransition classNames={'fade'} key={location.pathname} timeout={500}>
                    <Routes location={location}>
                        <Route path='/' element={<MainPage></MainPage>}></Route>
                        <Route path='/*' element={
                            <>
                                <InnerRoute></InnerRoute>
                                <Navbar></Navbar>
                            </>
                        }>
                        </Route>
                    </Routes>
                </CSSTransition>
            </TransitionGroup>

        </>
    );
}

export default RouterApp;