import React from 'react';
import { useDispatch } from 'react-redux';
import { OpenModal } from 'store/modal/action';
import PortfolidModal from 'pages/PortfolioModal';


function Item({ Img = '', Icon = '', Name = '', Label = [], Detail = '' }) {
    const dispatch = useDispatch();

    function ItemOnClick() {
        dispatch(OpenModal(<PortfolidModal Skill={Label} Icon={[Icon]} ImgArray={[Img]} Name={Name} Detail={Detail} ></PortfolidModal>));
    }

    return (
        <div className="Item" onClick={ItemOnClick}>
            <img src={Img} className='background' alt="" />
            <div className="TextBox">
                <img src={Icon} className='FirstLoading logo' alt="" />
                <h1 className='ellipsis'>{Name}</h1>
                <div className="LabelBox">
                    {
                        Label.map((value, index) => {
                            return (
                                <label key={index}>{value}</label>
                            )
                        })
                    }
                </div>
            </div>
        </div>
    )
}

export default Item;