import React from 'react';

function PortfolidModal({ Icon = [], ImgArray = [], Name = '', Detail = '', Skill = [] }) {
    return (
        <div className="PortfolioModal">
            <div className="ImageBox">
                <div className="TopShadow"></div>
                {
                    ImgArray.map((value, index) => {
                        return (
                            <img src={value} key={index} alt="" />
                        )
                    })
                }
                <div className="BottomShadow"></div>
            </div>
            <div className="TextBox">
                <h1>{Name}</h1>
                <p className='Label w7 green'></p>
                <p>
                    <img src={Icon} className='Icon' alt="" />
                </p>
                <p className='Label w7 green'> </p>
                <p>{Detail}</p>
                <p className='Label w7 green'> </p>
                <p>{Skill.toString().replaceAll(',', ', ')}</p>
            </div>
        </div>
    )
}

export default PortfolidModal;