import * as Actions from './action';

const initState = {
    isOpen: false,
    component: null
}

export function ModalReducer(state = initState, action) {
    switch (action.type) {
        case Actions.OPEN_MODAL:
            return {
                ...state,
                isOpen: true,
                component: action.payload
            }
        case Actions.CLOSE_MODAL:
            return {
                ...state,
                isOpen: false,
                component: null
            }
        default:
            return state;
    }
}