export const OPEN_MODAL = 'ui/OPEN_MODAL';
export const CLOSE_MODAL = 'ui/CLOSE_MODAL';

export const OpenModal = (component) => {
    return {
        type: OPEN_MODAL,
        payload: component
    }
}

export const CloseModal = () => {
    return {
        type: CLOSE_MODAL
    }
}