import React from 'react';
import 'styles/pages/PortfolioPage.scss';
import { useDispatch, useSelector } from 'react-redux';
import { Portfolio } from 'assets/portfolio';
import RocketImg from 'assets/imgs/rocket.svg';
import MedalImg from 'assets/imgs/medal.svg';
import Modal from 'components/Modal';
import Item from 'components/PortfolioGridItem';
import { OpenModal } from 'store/modal/action';
import PrizeModal from './PrizeModal';

function PortfolioPage() {
    const state = useSelector((state) => state.Modal);
    const dispatch = useDispatch();

    function PrizeOnClick() {
        dispatch(OpenModal(<PrizeModal></PrizeModal>));
    }

    return (
        <>
            <div className='PortfolioPage'>
                <section className='TitleSection'>
                    <h1 className='w6'> <br /><span className="green w7"> </span><br />.</h1>
                    <div className="Effect">
                        <div className="TextBox">
                              ,<br />SW     <br />   .
                        </div>
                        <div className="TimeLine">
                            <div className="Circle">
                            </div>
                            <div className="line"></div>
                            <div className="Circle">
                            </div>
                        </div>
                        <div className="TimeLineArea">
                                <h3 className='Event w4'>2020<br/> </h3>
                                <h3 className='Event w4' style={{textAlign:"right"}}>{new Date().getFullYear()}<br/></h3>
                        </div>
                    </div>
                </section>
                <section className='Content'>
                    <div className="Prize" onClick={PrizeOnClick}>
                        <img className='Rocket' src={RocketImg} alt="" />
                        <img className='Medal' src={MedalImg} alt="" />
                        <h2> </h2>
                        <p>   .</p>
                    </div>

                    {
                        Portfolio.map((value, index) => {
                            return (
                                <Item key={index} Img={value.Img} Icon={value.Logo} Name={value.Name} Detail={value.Detail} Label={value.Label} ></Item>
                            )
                        })
                    }
                </section>
            </div >
            {
                state.isOpen ? <Modal></Modal> : null
            }
        </>
    );
}

export default PortfolioPage;